	module.exports = "<h2 id=overview>Overview</h2> <p>Split layouts are meant for use in positioning child elements horizontally, with one of the children being used as primary content area, and the other(s) being used as a secondary content area.</p> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-l-split</code></td> <td><code>&lt;div&gt;</code>, <code>&lt;section&gt;</code>, or <code>&lt;article&gt;</code></td> <td>Initiates the split layout</td> </tr> <tr> <td><code>.pf-l-split__primary</code></td> <td><code>&lt;div&gt;</code></td> <td>Specificies the primary child of the layout, which expands vertically when needed. The primary child will expand horizontally to fill all space not needed by the secondary children. If the primary is not explicitly set, the second child will act as the primary child.</td> </tr> <tr> <td><code>.pf-l-split__secondary</code></td> <td><code>&lt;div&gt;</code></td> <td>Specifies a secondary child of the layout, which accommodates to its content. A split can have one or more secondary children. If secondary children are not explicitly set, all children but the second will act as secondary children.</td> </tr> <tr> <td><code>.pf-m-gutter</code></td> <td><code>.pf-l-split</code></td> <td>Adds space between children</td> </tr> </tbody> </table> ";

